// Colors
// -------------------------
@brand-primary:			#25a1c6;
@brand-add1:			#98a7ab;
@brand-add2:			#f7f7f7;
@white:					#fff;
@black:					#000;
@special-text-color:	#179ad1;
@contact-color:			#6a7376;
@field-color:			#AFACAC;
@brand-red:				#ff6268;
@brand-red-hover:		#e63456;
@brand-blue:			#00aeef;
@brand-blue-hover:		#0285df;
@brand-green:			#34b162;
@brand-green-hover:		#269d52;
@brand-yellow:			#ffc64c;
@brand-yellow-hover:	#faa940;
@disabled:				rgba(0,0,0,0.2);
// Scaffolding
// -------------------------
@body-bg:               @white;
@text-color:            @black;
@inactive-color:   		rgba(255,255,255,.2);
//Links
// -------------------------
@link-color:			#697e84;
@error-color: 			#ff4d62;
//Margins & height
// -------------------------
@default-margin:		10px;
@minHeight:				700px;

// Typography
// -------------------------
@font-family:  			'Roboto', Arial, sans-serif;
@font-weight-reg:      	400;
@font-weight-base:      500;
@font-weight-bold:      700;

@fs-base:       		0.8125rem; 	//13px
@fs-large:       		1.125rem; 	//18
@fs-small:       		0.75rem; 	//12px

@font-size-h1:          1.5rem; 	//24px
@line-height-h1:        1.75rem; 	//28

// Components
// -------------------------
@border-radius-base:  	4px;
@border-radius-large:   6px;
@border-radius-small:   3px;


// Sizes
// -------------------------
@sm-size: 				30px;
@field-size: 			30px;
@row-margin:			20px;